:root {
  --main-font: "Montserrat", sans-serif;
  --secondary-font: "Seymour One", sans-serif;
  --third-font: "Inter", sans-serif;
  --main-txt-cl: #fcfcfc;
  --accent-txt-cl: #131313;
  --bright-txt-cl: #f8d24e;
  --address-txt-color: #d6936d;
  --light-txt-color: #ababab;
  --accent-bg-cl: #84a178;
  --btn-bg-color: #f8d24e;
  --btn-txt-dark-color: #000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--main-font);
  color: var(--main-txt-cl);
  font-size: 16px;
  line-height: 1.75;
}

ul {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn, .btn-buy, .btn-read {
  width: 114px;
  height: 42px;
  background: var(--btn-bg-color);
  color: var(--accent-txt-cl);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  display: flex;
}

.btn-buy, .btn-read {
  width: 152px;
  color: var(--btn-txt-dark-color);
  line-height: 1.21429;
}

.btn-read {
  width: 164px;
  line-height: 1.28571;
}

